import React from "react";
import {BrowserRouter as Router, Route, Routes} from "react-router-dom";
import Home from "./routes/Movie/Home";
import MovieDetail from "./routes/MovieDetail";
import TvDetail from "./routes/TvDetail";
import Navigation from "./components/Navigation";
import Dashboard from "./layout/Dashboard";
import Login from "./routes/Login/Login"
import Signup from "./routes/Login/Signup";

function App() {
  return(
    <Router>
       {/* <HashRouter> */}
      {/* <Navigation /> */}

      <Routes>
        {/* React Router v6 Route component   element  , exact  */}
        {/* exact  url    
        v6      exact   . */}
      
      <Route path="/movie_app" element={<Dashboard />} /> 
      {/*      React Router Dashboard  .
          ,   URL /    DAshboard . 
          1) localhost:3000/2 => localhost:3000/#/
          2) localhost:3000/ => localhost:3000/#/
      */}
 
      <Route path="/movie_app/login" element={<Login/>} />
      <Route path="/movie_app/signup" element={<Signup/>} />
      <Route path="/movie/:id" element={<MovieDetail/>} />
      <Route path="/tv/:id" element={<TvDetail/>} />
    
      </Routes>
       {/* </HashRouter> */}
    </Router>
    
  ); 
}

export default App;