import React, { useState, useEffect } from "react"; //React   Hooks useEffect useState 
import axios from "axios";
import MovieLink from "../../components/MovieLink";
import "./Home.css";


// Home  fetching    
//    .      .
const Home = () => {
  // Home     
  //   ->       , React        
  const [isLoading, setIsLoading] = useState(true); // isLoading   true ,    
  const [movies, setMovies] = useState([]); // movies        
  const [genres, setGenres] = useState({}); // genres        

  const getGenres = async () => {
    //  getGenres  API    
    const { //      
      data: { genres }, //  data->genres-> id...
    } = await axios.get(
      //API    (promise )  genres  
      "https://api.themoviedb.org/3/genre/movie/list?api_key=250604987b9bcb91e2f812b87db35ebf&language=ko"
    ); // genres API        .

    const genreMap = {}; // ID      
    genres.forEach((genre) => {
      //   genreMap ID  
      genreMap[genre.id] = genre.name;
    });
    setGenres(genreMap); //genreMap genres     
 
  };

  const getMovies = async () => {
    //    (     .)
    if (Object.keys(genres).length === 0) {
      await getGenres(); //   
    }
    //  getMovies    API .
    const {
      data: { results },
    } = await axios.get(
      //API       results  
      "https://api.themoviedb.org/3/movie/popular?language=ko&region=kr&api_key=250604987b9bcb91e2f812b87db35ebf"
    );

    const moviesWithGenres = results.map((movie) => ({ //        moviesWithGenres 
      ...movie, //...      
      genres: movie.genre_ids.map((id) => genres[id] || id).join(", "), //  ID   
    }));

    setMovies(moviesWithGenres); //   movies  
    setIsLoading(false); //   isLoading false 
  };

  useEffect(() => {
    //  () getGenres getMovies   
    const fetchData = async () => {
      //         
      await getGenres();
      await getMovies();
    };
    fetchData(); //   fetchData   
  }, []); //      & genres   

    useEffect(() => {
    //genres        .  genres   ,       .
    if (Object.keys(genres).length > 0) { 
      getMovies(); // genres    
    }
  }, [genres]); // genres   

  return (
    //JSX  UI 
    <section className="container">
      {isLoading ? ( //isLoading      ,      ( . (? A:B) )
        <div className="loader">
          <span className="loader_text">Loading...</span>
        </div>
      ) : (
        <div className="movies">
          {movies.map((movie) => ( //movies      MovieLink  
              <MovieLink
                key={movie.id}
                id={movie.id}
                title={movie.title}
                summary={movie.overview.slice(0, 100)}
                poster={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                release_date={movie.release_date}
                rating={movie.vote_average + ""}
                genres={movie.genres}
              />
            )
          )}
        </div>
      )}
    </section>
  );
};

export default Home;

