import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom"; // Link  
import "./MyPage.css"; //     .

const MyPage = () => {
  const [isLogin, setIsLogin] = useState(false);
  const userId = sessionStorage.getItem("user_id");
  const [movies, setMovies] = useState([]);
  const [tvShows, setTvShows] = useState([]);

  useEffect(() => {
    //  ID    true 
    if (userId) {
      setIsLogin(true);
      const favoritesFromStorage = JSON.parse(sessionStorage.getItem("favorites")) || [];
      setMovies(favoritesFromStorage.filter(item => item.type === "movie")); //  
      setTvShows(favoritesFromStorage.filter(item => item.type === "tvShows")); // TV  
    }
  }, [userId]);

  return (
    <div>
      <h1></h1>
      
      <p>{isLogin ? " " : "  "}</p>

      {isLogin && (
        <>
        <div>
            <h2>  </h2>
            {movies.length > 0 ? (
                <div className="favorites-container">
                {movies.map((movie, index) => (
                    <div key={index} className="movie-item">
                    <Link to={`/movie/${movie.id}`}> {/*     */}
                    <h3>{movie.title}</h3>
                    </Link> 
                    <img src={movie.poster} alt={movie.title} className="movie-poster" /> 
                    </div>
                ))}
                </div>
            ) : (
                <p>  .</p>
            )}
        </div>

        <div>
        <h2> TV </h2>
            {tvShows.length > 0 ? (
                <div className="favorites-container">
                {tvShows.map((tv, index) => (
                    <div key={index} className="movie-item">
                    <Link to={`/tv/${tv.id}`}> {/*     */}
                    <h3>{tv.name}</h3>
                    </Link> 
                    <img src={tv.poster} alt={tv.name} className="movie-poster" /> 
                    </div>
                ))}
                </div>
            ) : (
                <p> TV  .</p>
            )}
        </div>
          
        </>
      )}
    </div>
  );
};

export default MyPage;
