import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import axios from "axios";
import MovieLink from "../../../components/MovieLink"; // MovieLink  
import "./Home.css";

const GenreMovies = ({ genreId }) => {
  const [isLoading, setIsLoading] = useState(true);
  const [movies, setMovies] = useState([]);
  const [genres, setGenres] = useState([]);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);

  //   
  useEffect(() => {
    const fetchGenres = async () => {
      try {
        const { data } = await axios.get(
          `https://api.themoviedb.org/3/genre/movie/list`,
          {
            params: {
              language: "ko",
              api_key: '250604987b9bcb91e2f812b87db35ebf',
            },
          }
        );
        setGenres(data.genres);
      } catch (error) {
        console.error("Error fetching genres:", error);
      }
    };

    fetchGenres();
  }, []);

  //   
  useEffect(() => {
    const fetchMovies = async () => {
      if (!hasMore) return; //      

      setIsLoading(true);
      try {
        const { data: { results, total_pages } } = await axios.get(
          `https://api.themoviedb.org/3/discover/movie`,
          {
            params: {
              with_genres: genreId,
              language: "ko",
              region: "kr",
              api_key: '250604987b9bcb91e2f812b87db35ebf',
              sort_by: "vote_count.desc",
              page,
            },
          }
        );

        setMovies((prevMovies) => {
          const newMovies = results.filter(result => !prevMovies.some(prev => prev.id === result.id));
          return [...prevMovies, ...newMovies];
        });

        setHasMore(page < total_pages); //    
      } catch (error) {
        console.error("Error fetching movies:", error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchMovies();
  }, [genreId, page]);

  //   
  const getGenreNames = (genreIds) => {
    return genreIds.map((id) => {
      const genre = genres.find((g) => g.id === id);
      return genre ? genre.name : '';
    }).join(", ");
  };

  //   
  const handleScroll = () => {
    const scrollPosition = window.innerHeight + window.scrollY;
    const documentHeight = document.body.offsetHeight;

    //       
    if (scrollPosition >= documentHeight - 200 && hasMore && !isLoading) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [hasMore, isLoading]);

  // genreId        
  useEffect(() => {
    //     
    setMovies([]); //   
    setPage(1); //  1 
    setHasMore(true); //       
    window.scrollTo(0, 0); //   

    // API     
    fetchMovies(); //      
  }, [genreId]);

  const fetchMovies = async () => {
    setIsLoading(true);
    try {
      const { data: { results, total_pages } } = await axios.get(
        `https://api.themoviedb.org/3/discover/movie`,
        {
          params: {
            with_genres: genreId,
            language: "ko",
            region: "kr",
            api_key: '250604987b9bcb91e2f812b87db35ebf',
            sort_by: "vote_count.desc",
            page: 1, //   1 
          },
        }
      );

      setMovies(results); //    
      setHasMore(1 < total_pages); //    
    } catch (error) {
      console.error("Error fetching movies:", error);
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading && page === 1) {
    return <div className="loader">Loading...</div>; //    
  }

  return (
    <section className="container">
      {movies.length > 0 ? (
        <div className="movies">
          {movies.map((movie) => (
            <MovieLink
              key={movie.id} //  ID 
              id={movie.id}
              title={movie.title}
              summary={movie.overview.slice(0, 100)}
              poster={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
              release_date={movie.release_date}
              rating={`${movie.vote_average}`}
              genres={getGenreNames(movie.genre_ids)}
            />
          ))}
        </div>
      ) : (
        <div>   .</div> //    
      )}
    </section>
  );
};

GenreMovies.propTypes = {
  genreId: PropTypes.number.isRequired,
};

export default GenreMovies;
