import React, { useEffect, useState, useRef } from "react";
import PropTypes from "prop-types";
import axios from "axios";
import TvLink from "../../../components/TvLink"; // MovieLink  
import "./Home.css";

const GenreTvShows = ({ genreId }) => {
  const [isLoading, setIsLoading] = useState(true); //  
  const [tvShows, setTvShows] = useState([]); // TV   
  const [genres, setGenres] = useState([]); //   
  const [page, setPage] = useState(1); //  
  const [hasMore, setHasMore] = useState(true); //     
  const observer = useRef(); // Intersection Observer  ref
  const sectionRef = useRef(null); //   ref
  //   
  useEffect(() => {
    const fetchGenres = async () => {
      try {
        const { data } = await axios.get(
          `https://api.themoviedb.org/3/genre/tv/list`,
          {
            params: {
              language: "ko",
              api_key: '250604987b9bcb91e2f812b87db35ebf',
            },
          }
        );
        setGenres(data.genres); //   
      } catch (error) {
        console.error("Error fetching genres:", error);
      }
    };

    fetchGenres(); //   
  }, []); //    

  // TV   
  const fetchTvShows = async () => {
    
    if (!hasMore) return; //    TV   

    setIsLoading(true); //  
    try {
      const { data: { results, total_pages } } = await axios.get(
        `https://api.themoviedb.org/3/discover/tv`,
        {
          params: {
            with_genres: genreId,
            language: "ko",
            region: "kr",
            api_key: '250604987b9bcb91e2f812b87db35ebf',
            sort_by: "vote_count.desc",
            page,
          },
        }
      );

      setTvShows((prevTvShows) => [
        ...prevTvShows,
        ...results.filter(result => !prevTvShows.some(prev => prev.id === result.id)),
      ]); //  TV    

      setHasMore(page < total_pages); //    
    } catch (error) {
      console.error("Error fetching TV shows:", error);
    } finally {
      setIsLoading(false); //  
    }
  };

  //   
  const getGenreNames = (genreIds) => {
    return genreIds.map((id) => {
      const genre = genres.find((g) => g.id === id);
      return genre ? genre.name : '';
    }).join(", "); //     
  };

  // Intersection Observer 
  const lastTvShowRef = useRef();
  useEffect(() => {
    if (observer.current) {
      observer.current.disconnect(); //   
    }

    observer.current = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting && hasMore && !isLoading) {
        setPage(prevPage => prevPage + 1); //  
      }
    });

    if (lastTvShowRef.current) {
      observer.current.observe(lastTvShowRef.current); //  TV   
    }
  }, [hasMore, isLoading]);


  
  // 1.    
  useEffect(() => {
    
    setTvShows([]); // TV   
    setPage(1); //  1 
    setHasMore(true); //     TV   
    if (sectionRef.current) {
      sectionRef.current.scrollIntoView({ behavior: "smooth" }); //  
    }
    
    //fetchTvShows(); //   
  }, [genreId]);

  //2.    TV   
  useEffect(() => {
    
    fetchTvShows(); // TV   
  }, [page]); //    

  // 3.genreId   TV   
  useEffect(() => {
     
    if (page === 1) {
      
      fetchTvShows(); // genreId     
    }
  }, [genreId]);

  

  if (isLoading && page === 1) {
    return <div className="loader">Loading...</div>; //    
  }

  return (
    <section ref={sectionRef} className="container">
      {tvShows.length > 0 ? (
        <div className="tv-shows">
          {tvShows.map((tvShow, index) => {
            if (tvShows.length === index + 1) {
              return (
                <div ref={lastTvShowRef} key={tvShow.id}>
                  <TvLink
                    id={tvShow.id}
                    name={tvShow.name}
                    summary={tvShow.overview.slice(0, 100)}
                    poster={`https://image.tmdb.org/t/p/w500${tvShow.poster_path}`}
                    release_date={tvShow.first_air_date}
                    rating={`${tvShow.vote_average}`}
                    genres={getGenreNames(tvShow.genre_ids)}
                  />
                </div>
              );
            }
            return (
              <TvLink
                key={tvShow.id}
                id={tvShow.id}
                name={tvShow.name}
                summary={tvShow.overview.slice(0, 100)}
                poster={`https://image.tmdb.org/t/p/w500${tvShow.poster_path}`}
                release_date={tvShow.first_air_date}
                rating={`${tvShow.vote_average}`}
                genres={getGenreNames(tvShow.genre_ids)}
              />
            );
          })}
        </div>
      ) : (
        <div> .</div> // TV   
      )}
    </section>
  );
};

GenreTvShows.propTypes = {
  genreId: PropTypes.number.isRequired, // genreId prop  
};

export default GenreTvShows;
