import React from "react";
import PropTypes from "prop-types";
import { useNavigate } from "react-router-dom"; // useNavigate 
import "./Movie.css";

function Movie({ id, title, summary, poster, release_date, rating, genres }) {
  // id    
  const navigate = useNavigate(); // useNavigate  
  const isLoggedIn = !!sessionStorage.getItem("user_id"); //   
  //getItem("user_id") : user_id    .  null 
  /**!! (Double Negation) :  boolean  
   *  ! not  null !null true    ! false .
   *  !  .  user_id  true,  false .
   * ,   "user_id"   , isLoggendIn true 
   *    false .
  */

  const handleAddToFavorites = () => {

    if (!isLoggedIn) {
      alert(" .");
      navigate("/movie_app/login"); //   
      return;
    }

    const favorites = JSON.parse(sessionStorage.getItem("favorites")) || [];
    //      ( id    movie)
    const isAlreadyFavorite = favorites.some(
      (movie) => movie.id === id && movie.type === "movie"
    );
    if (!isAlreadyFavorite) {
      favorites.push({
        id,
        title,
        summary,
        poster,
        release_date,
        rating,
        type: "movie",
      });
      sessionStorage.setItem("favorites", JSON.stringify(favorites));
      alert(`${title}()   .`);
    } else {
      alert(`${title}()    .`);
    }
  };

  const handleRemoveFromFavorites = () => {
    if (!isLoggedIn) {
      alert(" .");
      navigate("/movie_app/login"); //   
      return;
    }

    const favorites = JSON.parse(sessionStorage.getItem("favorites")) || [];
    //     
    const isFavorite = favorites.some(
      (movie) => movie.id === id && movie.type === "movie"
    );
    if (isFavorite) {
      const updatedFavorites = favorites.filter((movie) => movie.id !== id);
      sessionStorage.setItem("favorites", JSON.stringify(updatedFavorites));
      alert(`${title}()   .`);
    } else {
      alert(`${title}()    .`);
    }
  };

  return (
    <div className="movie">
      <img
        src={poster}
        alt={title}
        title={title}
        style={{ width: "300px", height: "auto", marginBottom: "-20px"   }}
      />
      <br />
      {isLoggedIn ? (
        <>
          <button onClick={handleAddToFavorites}></button>
          <button
            onClick={handleRemoveFromFavorites}
            style={{ marginLeft: "20px" }}
          >
            
          </button>
        </>
      ) : (
        <p>   .</p>
      )}
      <div className="movie__data">
        <br />
        <h3 className="movie__title">{title}</h3>
        <h5 className="movie__release">{release_date}</h5>
        <p className="movie__rating">Rating: {rating}</p>
        <p className="movie__genres"> {genres} </p>
        <p className="movie__summary">{summary}</p>
      </div>
    </div>
  );
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  release_date: PropTypes.string.isRequired,
  rating: PropTypes.string.isRequired,
};

export default Movie;
