import React, { useState, useEffect } from "react"; //React   Hooks useEffect useState 
import axios from "axios";
import TvLink from "../../components/TvLink"; // MovieLink TvLink 
import "./Home.css";

// Home  fetching    
//    .   TV    .
const Home = () => {
  // Home     
  const [isLoading, setIsLoading] = useState(true); // isLoading   true ,    
  const [tvShows, setTvShows] = useState([]); // movies      TV   
  const [genres, setGenres] = useState({}); // genres        

  const getGenres = async () => {
    //   getGenres  API TV   
    const { 
      data: { genres }, 
    } = await axios.get(
      // API    (promise )  genres  
      "https://api.themoviedb.org/3/genre/tv/list?api_key=250604987b9bcb91e2f812b87db35ebf&language=ko"
    );

    const genreMap = {}; //  ID      
    genres.forEach((genre) => {
      //    genreMap ID  
      genreMap[genre.id] = genre.name;
    });
    setGenres(genreMap); // genreMap genres     
  };

  const getTvShows = async () => {
    //    
    if (Object.keys(genres).length === 0) {
      await getGenres(); //   
    }
    //   getTvShows   TV  API 
    const {
      data: { results },
    } = await axios.get(
      // API    TV    results  
      "https://api.themoviedb.org/3/trending/tv/day?api_key=250604987b9bcb91e2f812b87db35ebf&language=ko&region=ko"
    );

    const tvShowsWithGenres = results.map((show) => ({ //  TV        tvShowsWithGenres 
      ...show,
      genres: show.genre_ids.map((id) => genres[id] || id).join(", "), //  ID   
    }));

    setTvShows(tvShowsWithGenres); //  TV   tvShows  
    setIsLoading(false); //    isLoading false 
  };

  useEffect(() => {
    //   () getGenres getTvShows   
    const fetchData = async () => {
      await getGenres();
      await getTvShows();
    };
    fetchData(); //    fetchData   
  }, []); //      & genres   


  useEffect(() => {
    //genres        .  genres   ,       .
    if (Object.keys(genres).length > 0) { 
      getTvShows(); // genres    
    }
  }, [genres]); // genres   


  return (
    // JSX  UI 
    <section className="container">
      {isLoading ? ( // isLoading      ,   TV   
        <div className="loader">
          <span className="loader_text">Loading...</span>
        </div>
      ) : (
        <div className="tv-shows"> {/*   movies tv-shows  */}
          {tvShows.map((show) => ( // tvShows    TV   TvLink  
            <TvLink
              key={show.id}
              id={show.id}
              name={show.name} // title name 
              summary={show.overview.slice(0, 100)}
              poster={`https://image.tmdb.org/t/p/w500${show.poster_path}`}
              first_air_date={show.first_air_date} // release_date first_air_date 
              rating={show.vote_average + ""}
              genres={show.genres}
            />
          ))}
        </div>
      )}
    </section>
  );
};

export default Home;
